
.benefits {
  padding: 100px 0;
  background-color: $color-light-grey;

  .benefits-container {
    padding: 0 100px;
    display: flex;
    flex-direction: column;
    align-items: center;

    .benefits-row {
      width: 1124px;
      height: 370px;
      background-color: $color-white;
      border: 1px solid #E8E9ED;
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      margin-bottom: 37px;
      border-radius: 5px;

      .benefits__textarea {
        max-width: 416px;
        margin: 90px 36px;

        &::after {
          content: "";
          width: 332px;
          height: 4px;
          //background-color: var(--accent-color);
          display: block;
          margin-top: 50px;
        }


        .benefits__title {
          text-align: left;
          font-family: $font-raleway;
          font-size: 2rem;
          color: $color-black;
          margin-bottom: 30px;
        }

        .benefits__description {
          margin-top: 30px;
          line-height: 1.375rem;
        }

      }

      .benefits__image {
        position: relative;

        .benefits__image img {
          position: relative;
          z-index: 2;
        }

      }

    }

    .benefits-row--notebook {

      .benefits__image {
        margin-right: 117px;
        margin-bottom: 38px;

        img {
          width: 386px;
          height: auto;
        }

        &:before {
          content: "";
          width: 180px;
          height: 180px;
          background-color: $color-redish;
          display: block;
          border-radius: 40px;
          position: absolute;
          right: 0;
          top: -40px;
          transform: rotate(45deg);
        }
      }

    }


    .benefits-row--phone {
      flex-direction: row-reverse;
      padding: 0 20px 0 70px;

      .benefits__image {
        img {
          width: 426px;
          height: auto;
        }

        &:before {
          content: "";
          width: 320px;
          height: 320px;
          background-color: $color-primary;
          display: block;
          border-radius: 50%;
          position: absolute;
          left: 0;
          top: -20px;
        }

        .benefits__textarea {
          margin: 70px 0;
        }

      }
    }

    .benefits-double {
      display: flex;
      //--accent-color: var(--color-green);
      //.benefits__double > div {
      //  margin-bottom: 0;
      //
      //}

      .benefits-row--solo {
        max-width: 620px;

        .benefits__textarea {
          max-width: none;
          margin: 70px 35px;
        }
      }

      .benefits-row--image {
        max-width: 440px;
        flex-grow: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: $color-green;
        border: none;
        margin-left: 60px;

        img {
          width: 295px;
          height: auto;
          margin-top: 55px;
        }
      }


    }
  }
}

